import React from "react";

const Resume = () => {
  return (
    <div className="p-10">
      <h2 className="text-3xl font-bold">My Resume</h2>
      <p className="mt-2 text-lg">
        I have worked with various technologies to build end-to-end solutions, focusing on full-stack development, machine learning, and mobile app development. Below is a brief overview of my career and skills.
      </p>
      <p className="mt-4 text-lg">
        **Skills:**
        <ul className="list-disc pl-5">
          <li>Full-Stack Development (React, Angular, Node.js)</li>
          <li>Machine Learning (Python, TensorFlow, Keras)</li>
          <li>Mobile App Development (Android Studio, React Native)</li>
          <li>Cloud Computing (AWS, Firebase)</li>
        </ul>
      </p>
      <p className="mt-4 text-lg">Download my resume below:</p>
      <a href="/resume.pdf" download className="bg-blue-500 text-white px-4 py-2 mt-4 inline-block rounded">
        Download Resume
      </a>
    </div>
  );
};

export default Resume;
