import React, { useState, useEffect } from "react";
import { db, storage } from "../firebaseConfig";
import { doc, getDoc, setDoc, collection, addDoc, getDocs, deleteDoc } from "firebase/firestore";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";

const Dashboard = () => {
  const [name, setName] = useState("");
  const [bio, setBio] = useState("");
  const [profilePic, setProfilePic] = useState(null);
  const [imageURL, setImageURL] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [skills, setSkills] = useState([]);
  const [newSkill, setNewSkill] = useState("");
  // Project states
  const [projects, setProjects] = useState([]);
  const [projectTitle, setProjectTitle] = useState("");
  const [projectDescription, setProjectDescription] = useState("");
  const [projectLink, setProjectLink] = useState("");
  const [projectImage, setProjectImage] = useState(null);
  const [projectImageURL, setProjectImageURL] = useState("");

  useEffect(() => {
    fetchUserData();
    fetchSkills();
    fetchProjects();
  }, []);

  const fetchUserData = async () => {
    const userRef = doc(db, "admin", "profile");
    const docSnap = await getDoc(userRef);
    if (docSnap.exists()) {
      const userData = docSnap.data();
      setName(userData.name || "");
      setBio(userData.bio || "");
      setImageURL(userData.imageURL || "");
    }
  };
  const fetchSkills = async () => {
    const querySnapshot = await getDocs(collection(db, "skills"));
    setSkills(querySnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })));
  };

  const fetchProjects = async () => {
    const projectsCollection = collection(db, "projects");
    const projectsSnapshot = await getDocs(projectsCollection);
    const projectsList = projectsSnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    setProjects(projectsList);
  };

  const updateProfile = async () => {
    await setDoc(doc(db, "admin", "profile"), { name, bio, imageURL }, { merge: true });
    alert("Profile Updated");
  };
  const addSkill = async () => {
    if (newSkill) {
      await addDoc(collection(db, "skills"), { name: newSkill });
      setSkills([...skills, { name: newSkill }]);
      setNewSkill("");
    }
  };
  const deleteSkill = async (skillId) => {
    const confirmDelete = window.confirm("Are you sure you want to delete this skill?");
    if (confirmDelete) {
      await deleteDoc(doc(db, "skills", skillId));
      alert("Skill deleted!");
      fetchSkills();
    }
  };
  

  const uploadImage = async () => {
    if (!profilePic) {
      setError("Please select an image before uploading.");
      return;
    }

    setLoading(true);
    setError("");

    try {
      const imageRef = ref(storage, `profile_pictures/admin`);
      await uploadBytes(imageRef, profilePic);
      const url = await getDownloadURL(imageRef);
      setImageURL(url);

      await setDoc(doc(db, "admin", "profile"), { imageURL: url }, { merge: true });
    } catch (err) {
      setError("Error uploading image. Please try again.");
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const addProject = async () => {
    if (!projectTitle || !projectDescription || !projectLink) {
      setError("Project title, description, and link are required.");
      return;
    }

    let uploadedImageURL = "";
    if (projectImage) {
      const imageRef = ref(storage, `project_images/${projectTitle}`);
      await uploadBytes(imageRef, projectImage);
      uploadedImageURL = await getDownloadURL(imageRef);
    }

    const newProject = {
      title: projectTitle,
      description: projectDescription,
      link: projectLink,
      image: uploadedImageURL,
    };

    await addDoc(collection(db, "projects"), newProject);
    alert("Project Added!");
    setProjectTitle("");
    setProjectDescription("");
    setProjectLink("");
    setProjectImage(null);
    setProjectImageURL("");
    fetchProjects(); // Refresh project list
  };

  const deleteProject = async (projectId) => {
    const confirmDelete = window.confirm("Are you sure you want to delete this project?");
    if (confirmDelete) {
      await deleteDoc(doc(db, "projects", projectId));
      alert("Project deleted!");
      fetchProjects();
    }
  };

  return (
    <div className="p-10">
      <h2 className="text-3xl font-bold">Dashboard</h2>
      <div>
        <label className="block font-semibold">Name:</label>
        <input type="text" value={name} onChange={(e) => setName(e.target.value)} className="border p-2 w-full" />
        
        <label className="block font-semibold">Bio:</label>
        <textarea value={bio} onChange={(e) => setBio(e.target.value)} className="border p-2 w-full"></textarea>
        
        <button onClick={updateProfile} className="bg-green-500 text-white px-4 py-2 mt-4 rounded">
          Save
        </button>

        <label className="block font-semibold mt-4">Profile Picture:</label>
        <input type="file" onChange={(e) => setProfilePic(e.target.files[0])} />
        <button onClick={uploadImage} className="bg-blue-500 text-white px-4 py-2 mt-4 rounded" disabled={loading}>
          {loading ? "Uploading..." : "Upload"}
        </button>

        {error && <p className="text-red-500 mt-2">{error}</p>}
        {imageURL && <img src={imageURL} alt="Profile" className="mt-4 w-32 h-32 rounded-full border" />}
      </div>
 {/* Skills Section */}
 <h2 className="text-2xl font-bold mt-6">Skills</h2>
      <div className="flex flex-wrap gap-4 mt-2">
        {skills.map((skill, index) => (
          <span key={index} className="bg-gray-200 px-3 py-1 rounded">{skill.name}</span>
        ))}
      </div>

      <input type="text" placeholder="New Skill" value={newSkill} onChange={(e) => setNewSkill(e.target.value)} className="border p-2 mt-2 w-full" />
      <button onClick={addSkill} className="bg-blue-500 text-white px-4 py-2 mt-2 rounded">
  Add Skill
</button>

      {/* Project Section */}
      <div className="mt-8">
        <h3 className="text-2xl font-semibold">Add New Project</h3>
        <input type="text" placeholder="Project Title" value={projectTitle} onChange={(e) => setProjectTitle(e.target.value)} className="border p-2 w-full mt-2" />
        <textarea placeholder="Project Description" value={projectDescription} onChange={(e) => setProjectDescription(e.target.value)} className="border p-2 w-full mt-2"></textarea>
        <input type="text" placeholder="Project Link (URL)" value={projectLink} onChange={(e) => setProjectLink(e.target.value)} className="border p-2 w-full mt-2" />
        <input type="file" onChange={(e) => setProjectImage(e.target.files[0])} className="mt-2" />
        <button onClick={addProject} className="bg-purple-500 text-white px-4 py-2 mt-4 rounded">Add Project</button>
      </div>

      {/* Display Existing Skills */}
      <h2 className="text-2xl font-bold mt-6">Skills</h2>
<div className="flex flex-wrap gap-4 mt-2">
  {skills.map((skill) => (
    <div key={skill.id} className="bg-gray-200 px-3 py-1 rounded flex items-center">
      <span>{skill.name}</span>
      <button 
        onClick={() => deleteSkill(skill.id)} 
        className="ml-2 text-red-500 text-sm font-bold"
      >
        
      </button>
    </div>
  ))}
</div>

      {/* Display Existing Projects */}
      <div className="mt-10">
        <h3 className="text-2xl font-semibold">Existing Projects</h3>
        {projects.map((project) => (
          <div key={project.id} className="border p-4 mt-4 flex justify-between items-center">
            <div>
              <h4 className="text-xl font-bold">{project.title}</h4>
              <p>{project.description}</p>
              <a href={project.link} target="_blank" rel="noopener noreferrer" className="text-blue-500 underline">
                View Project
              </a>
              {project.image && <img src={project.image} alt={project.title} className="mt-2 w-32 h-32 rounded" />}
            </div>
            <button onClick={() => deleteProject(project.id)} className="bg-red-500 text-white px-4 py-2 rounded">
              Delete
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Dashboard;
