import React from "react";

const Portfolio = () => {
  const projects = [
    {
      title: "Face Recognition System",
      description: "A web-based face recognition app using Flask and OpenCV. It helps with secure access control and identity verification.",
      technologies: "Technologies: Flask, OpenCV, Python, HTML, CSS, JavaScript",
    },
    {
      title: "Speech Emotion Detection",
      description: "A machine learning model to detect emotions from speech. It uses audio signal processing and deep learning techniques.",
      technologies: "Technologies: Python, TensorFlow, Keras, Scikit-learn",
    },
    {
      title: "Smart Garage Door System",
      description: "A smart garage door system using face recognition for secure access and a mobile app interface to control it.",
      technologies: "Technologies: Raspberry Pi, OpenCV, Flask, ESP8266, Firebase, MQTT",
    },
  ];

  return (
    <div className="p-10">
      <h2 className="text-3xl font-bold">My Projects</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
        {projects.map((project, index) => (
          <div key={index} className="p-4 bg-gray-100 rounded-lg shadow-md">
            <h3 className="text-xl font-semibold">{project.title}</h3>
            <p className="text-gray-700 mt-2">{project.description}</p>
            <p className="text-gray-600 mt-2">{project.technologies}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Portfolio;
