import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Menu, X } from "lucide-react";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-gray-800 text-white p-4">
      <div className="container mx-auto flex justify-between items-center">
        <h1 className="text-xl font-bold">My Portfolio</h1>

        {/* Mobile Menu Button */}
        <button
          className="md:hidden focus:outline-none"
          onClick={() => setIsOpen(!isOpen)}
        >
          {isOpen ? <X size={28} /> : <Menu size={28} />}
        </button>

        {/* Navigation Links */}
        <div
          className={`absolute md:static bg-gray-800 md:bg-transparent w-full md:w-auto left-0 md:flex md:space-x-6 flex-col md:flex-row items-center md:items-center transition-all duration-300 ${
            isOpen ? "top-16 p-4 flex" : "top-[-200px] hidden"
          }`}
        >
          <Link to="/" className="px-4 py-2 hover:text-gray-300">Home</Link>
          <Link to="/about" className="px-4 py-2 hover:text-gray-300">About</Link>
          <Link to="/portfolio" className="px-4 py-2 hover:text-gray-300">Portfolio</Link>
          <Link to="/resume" className="px-4 py-2 hover:text-gray-300">Resume</Link>
          <Link to="/contact" className="px-4 py-2 hover:text-gray-300">Contact</Link>
          <Link to="/dashboard" className="px-4 py-2 hover:text-gray-300">Dashboard</Link>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
