import React from "react";

const Contact = () => {
  return (
    <div className="p-10">
      <h2 className="text-3xl font-bold">Contact Me</h2>
      <p className="mt-2 text-lg">I would love to hear from you! Feel free to reach out via the following options:</p>
      <ul className="mt-4 text-lg">
        <li>Email: <a href="mailto:vamshi@example.com" className="text-blue-500">vamshikrishna9608@gmail.com</a></li>
        <li>LinkedIn: <a href="https://www.linkedin.com/in/vamshikrishna9608/" className="text-blue-500" target="_blank" rel="noopener noreferrer">linkedin.com/in/vamshikrishna9608</a></li>
        <li>GitHub: <a href="https://github.com/Vamshikrishna779" className="text-blue-500" target="_blank" rel="noopener noreferrer">github.com/Vamshikrishna779</a></li>
      </ul>
      <div className="mt-6">
        <form>
          <input
            type="text"
            placeholder="Your Name"
            className="p-2 border border-gray-300 rounded mb-2 w-full"
          />
          <input
            type="email"
            placeholder="Your Email"
            className="p-2 border border-gray-300 rounded mb-2 w-full"
          />
          <textarea
            placeholder="Your Message"
            className="p-2 border border-gray-300 rounded mb-2 w-full"
          />
          <button type="submit" className="bg-blue-500 text-white px-6 py-2 rounded-full mt-4">
            Send Message
          </button>
        </form>
      </div>
    </div>
  );
};

export default Contact;
